

body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #ffffff;
  color: #000000;
}

.app-container {
  width: 100%;
  height: 100vh;
}

.slide-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
}

h1, h2, h3 {
  margin: 10px 0;
}

.input-group {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-id-input {
  padding: 8px;
  font-size: 16px;
  width: 200px;
  text-align: center;
}

.primary-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #000;
  color: #fff;
  border: none;
  cursor: pointer;
}

.primary-button:hover {
  opacity: 0.9;
}

.questions-list {
  width: 100%;
  max-width: 600px;
}

.question-item {
  margin-bottom: 20px;
}

.question-text {
  font-weight: bold;
  margin-bottom: 5px;
}

.options-list {
  list-style-type: none;
  padding-left: 20px;
}

.options-list li {
  margin: 3px 0;
}

.feud-list p {
  margin: 3px 0;
}
